/* Tech Stack / Work Page Specific Styles */
/* Base layout styles only - theme styling is in themes/*.css */

p.work {
  font-size: 12px;
  color: #333333;
  line-height: 1.6;
}

/* Other work section */
.other-work {
  margin-top: 24px;
  padding: 16px;
  background: linear-gradient(180deg, #fafafa 0%, #f0f0f0 100%);
  border: 2px dashed #808080;
}

.other-work p {
  font-size: 12px;
  line-height: 1.8;
}

.other-work p * {
  vertical-align: middle;
}

/* Responsive adjustments */
@media screen and (max-width: 540px) {
  .projects {
    min-height: 200px;
    margin: 20px 0;
  }

  .old-projects.welcome ul {
    padding-left: 10px;
  }
}
